import { SubNavigation } from "@/components/sub-navigation";
import { getStudents, getStudentStats } from "@/actions/user-students";
import { GraduationCap, Users, UserCheck, TrendingUp, Search } from "lucide-react";
import Link from "next/link";

const subNavItems = [
    { label: "Overview", href: "/admin/users/students" },
    { label: "Active Students", href: "/admin/users/students/active" },
    { label: "On Leave", href: "/admin/users/students/on-leave" },
    { label: "Reports", href: "/admin/users/students/reports" },
];

export default async function StudentsPage() {
    // Fetch only current students (STUDYING)
    const [studentsResult, statsResult] = await Promise.all([
        getStudents({ status: "STUDYING" }),
        getStudentStats(),
    ]);

    const students = studentsResult.success ? studentsResult.students : [];
    const stats = statsResult.success ? statsResult.stats : {
        total: 0,
        studying: 0,
        graduated: 0,
        onLeave: 0,
    };

    // Calculate current students total (excluding graduated)
    const currentStudentsTotal = (stats.studying || 0) + (stats.onLeave || 0);

    return (
        <div className="flex h-full flex-col">
            <SubNavigation items={subNavItems} />

            <div className="flex-1 space-y-6 p-8">
                <div>
                    <h1 className="text-3xl font-bold tracking-tight">Students</h1>
                    <p className="text-muted-foreground">
                        Manage current student records, enrollment, and academic progress
                    </p>
                </div>

                {/* Stats Cards - Only Current Students */}
                <div className="grid gap-4 md:grid-cols-3">
                    <div className="rounded-lg border bg-card p-6">
                        <div className="flex items-center justify-between">
                            <div>
                                <p className="text-sm font-medium text-muted-foreground">Current Students</p>
                                <p className="text-2xl font-bold">{currentStudentsTotal}</p>
                            </div>
                            <Users className="h-8 w-8 text-blue-600" />
                        </div>
                    </div>

                    <div className="rounded-lg border bg-card p-6">
                        <div className="flex items-center justify-between">
                            <div>
                                <p className="text-sm font-medium text-muted-foreground">Studying</p>
                                <p className="text-2xl font-bold">{stats?.studying ?? 0}</p>
                            </div>
                            <UserCheck className="h-8 w-8 text-green-600" />
                        </div>
                    </div>

                    <div className="rounded-lg border bg-card p-6">
                        <div className="flex items-center justify-between">
                            <div>
                                <p className="text-sm font-medium text-muted-foreground">On Leave</p>
                                <p className="text-2xl font-bold">{stats?.onLeave ?? 0}</p>
                            </div>
                            <TrendingUp className="h-8 w-8 text-orange-600" />
                        </div>
                    </div>
                </div>

                {/* Search and Filters */}
                <div className="flex items-center gap-4">
                    <div className="relative flex-1">
                        <Search className="absolute left-3 top-1/2 h-4 w-4 -translate-y-1/2 text-muted-foreground" />
                        <input
                            type="text"
                            placeholder="Search by name or student ID..."
                            className="w-full rounded-md border border-input bg-background pl-10 pr-4 py-2 text-sm"
                        />
                    </div>
                    <Link href="/admin/users/students/create">
                        <button className="rounded-md bg-blue-600 px-4 py-2 text-sm text-white hover:bg-blue-700">
                            Add Student
                        </button>
                    </Link>
                </div>

                {/* Students Table */}
                <div className="rounded-lg border bg-card">
                    <div className="p-6">
                        <h2 className="text-lg font-semibold mb-4">Active Students ({students?.length || 0})</h2>

                        {students && students.length > 0 ? (
                            <div className="overflow-x-auto">
                                <table className="w-full">
                                    <thead className="border-b">
                                        <tr className="text-left text-sm text-muted-foreground">
                                            <th className="pb-3 font-medium">Student ID</th>
                                            <th className="pb-3 font-medium">Name</th>
                                            <th className="pb-3 font-medium">Program</th>
                                            <th className="pb-3 font-medium">Admission Year</th>
                                            <th className="pb-3 font-medium">Status</th>
                                            <th className="pb-3 font-medium">Actions</th>
                                        </tr>
                                    </thead>
                                    <tbody className="divide-y">
                                        {students.map((student: any) => (
                                            <tr key={student.id} className="text-sm">
                                                <td className="py-3 font-medium">{student.studentId}</td>
                                                <td className="py-3">
                                                    <div>
                                                        <p className="font-medium">{student.firstName} {student.lastName}</p>
                                                        <p className="text-xs text-muted-foreground">{student.user?.email}</p>
                                                    </div>
                                                </td>
                                                <td className="py-3">
                                                    <div>
                                                        <p>{student.program?.nameEn}</p>
                                                        <p className="text-xs text-muted-foreground">
                                                            {student.program?.faculty?.nameEn}
                                                        </p>
                                                    </div>
                                                </td>
                                                <td className="py-3">{student.admissionYear || "-"}</td>
                                                <td className="py-3">
                                                    <span className={`inline-flex items-center rounded-full px-2 py-1 text-xs font-medium ${student.status === "STUDYING"
                                                        ? "bg-green-100 text-green-700"
                                                        : "bg-orange-100 text-orange-700"
                                                        }`}>
                                                        {student.status}
                                                    </span>
                                                </td>
                                                <td className="py-3">
                                                    <Link
                                                        href={`/admin/users/students/${student.id}`}
                                                        className="text-blue-600 hover:underline text-sm"
                                                    >
                                                        View
                                                    </Link>
                                                </td>
                                            </tr>
                                        ))}
                                    </tbody>
                                </table>
                            </div>
                        ) : (
                            <div className="text-center py-12 text-muted-foreground">
                                <GraduationCap className="mx-auto h-12 w-12 mb-4 opacity-50" />
                                <p>No active students found</p>
                                <p className="text-sm">Student records will appear here</p>
                            </div>
                        )}
                    </div>
                </div>
            </div>
        </div>
    );
}
